.signUp {
    display: flex;
    flex-direction: column;
    margin: 10px 0 10px 0;
}
.signUpHeader {
    display: flex;
    background: #2c312e;
    color: white;
    /* padding: 20px 20px; */
    border-radius: 10px;
    width: 100%;
    justify-content: center;
}
.signUpHeader > h1 {
    font-weight: 400 !important;
    font-family: roboto !important;
    color: #e2e2e2;
}
.signUp__section {
    display: flex;
    box-shadow: 0px 1px 5px 0px rgb(0 0 0 / 20%), 0px 2px 2px 0px rgb(0 0 0 / 14%), 0px 3px 1px -2px rgb(0 0 0 / 12%);
    /* padding: 20px 20px; */
    margin-top: 10px;
    width: 100%;
    border-radius: 10px;
    flex-direction: column;
}

.signUp__form {
    width: 100% !important;
}
.step5__section > h4 {
    font-weight: 400;
}
.progress {
    margin: 20px 20px 0 20px;
}
.step2 {
    padding: 10px 10px 20px 20px;
}
.step5__section {
padding: 0 0 0 20px;
}
.spinnerDiv {
    display: flex;
    justify-content: space-around;
}
.step6_section {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.step6_section h4 {
    color: rgba(0,0,0,0.87);
    font-family: Roboto;
    font-size: 20px;
    font-weight: 500;
    letter-spacing: 0.15px;
    line-height: 24px;
    margin: 10px 10px 10px 10px;
}
.step6_section p {
    color: rgba(0,0,0,0.87);
    font-family: Roboto;
    font-size: 16px;
    letter-spacing: 0.15px;
    line-height: 24px;
    margin: 0 10px 20px 10px;
}
.step6_section__checkBar {
    color: #6200EE;
}
.step6_section_email__label {
    color: rgba(0,0,0,0.87);
    font-family: Roboto;
    font-size: 16px;
    font-weight: 800;
}